.menuIcon {
    color: #000;
    width: 22px;
    height: 2px;
    position: relative;
    background-color: currentColor;
}
  
.menuIcon:before {
    content: '';
    position: absolute;
    top: -5px;
    left: 0;
    width: 22px;
    height: 2px;
    background-color: currentColor;
}
  
.menuIcon:after {
    content: '';
    position: absolute;
    top: 5px;
    left: 0;
    width: 22px;
    height: 2px;
    background-color: currentColor;
}

